import React, {Component} from "react";
import PropTypes from 'prop-types'
import {getCartProductsFromEntry} from "eshop/modules/cart/analyzer/CartInfoUtils";
import Technology from "../../../../core/enum/Technology";

const ProductsFinePrintInfo = (props) => {
    return(
        <div className="l-full-row">
            <div className="l-page-row">
                <div className="u-padding-top u-padding">
                    <div dangerouslySetInnerHTML={{ __html: props.label }}/>
                </div>
            </div>
        </div>
    );
}

ProductsFinePrintInfo.propTypes = {
    label: PropTypes.string.isRequired
}

export const FinePrintForSatteliteTV = (props) => {
    if(props.entry.tvFixProduct && props.entry.tvFixProduct.technology === Technology.SAT){
        return (<ProductsFinePrintInfo
            label={props.label}
        />)
    } else {
        return null;
    }
}

export const FinePrintForADSLModem = (props) => {
    if(props.entry.broadbandFixProduct
        && props.entry.broadbandFixProduct.technology === Technology.ADSL
        && props.entry.bundleType === "1P_PRE"
    ) {
        return (<ProductsFinePrintInfo
            label={props.label}
        />)
    } else {
        return null;
    }
}

